
ion-toolbar {
  --background: rgb(76, 0, 255);
}
.sidebar-content {
  --padding-start: 0px;
  --padding-end: 0px;
  --padding-top: 0px;
  --padding-bottom: 0px;
}
.menu-item {
  border-bottom: 1px solid rgba(185, 185, 185, 0.404);
}
.router-link-active {
  color: rgb(76, 0, 255);
  background-color: rgb(211, 193, 255);
  width: 100%;
}
.user-icon {
  font-size: 80px;
}
.user-name {
  font-size: 20px;
  color: white;
  margin-top: 15%;
}
.icons {
  font-size: 30px;
  margin: 5%;
  color: rgb(94, 94, 94);
  float: left;
}
a {
  width: 100%;
}
.menu-title {
  margin: 6%;
  color: rgb(94, 94, 94);
  float: left;
}

ion-content {
  --background: rgb(241, 241, 241);
}
.mt-10 {
  margin-top: 100px;
}
.heading {
  color: white;
  margin-top: 5px;
}
.subtext {
  color: rgb(49, 49, 49);
  font-size: 15px;
}
ion-menu-button {
  color: white;
}
ion-grid {
  padding: 0px;
}
/* ion-modal {
  --width: 85% !important;
  --height: 75% !important;
} */
.add-modal {
  --width: 85% !important;
  --height: 75% !important;
}
.date-block {
  padding: 15px;
  background: #ebebeb;
  color: #656565;
  font-size: 17px;
}

.add-todo {
  --background: white;
}
.modal-heading {
  font-size: 25px !important;
  color: rgb(45, 0, 128);
}
ion-select {
  max-width: 100%;
  padding-left: 0px;
}
ion-textarea {
  --padding-top: 0px;
  border-bottom: 1px solid rgb(184, 184, 184);
}
.no-padding {
  padding: 0px;
}
ion-toggle {
  padding-top: 5px;
}
ion-item {
  --padding-start: 0px;
}
input[type="text"],
select {
  width: 100%;
  border: none;
  height: 30px;
  font-size: 18px;
}
textarea {
  width: 100%;
  border: none;
  font-size: 18px;
}
*:focus {
  outline: none;
}
input[type="checkbox"] {
  width: 18px;
  height: 18px;
}

h5 {
  margin-top: 5px;
  margin-bottom: 5px;
}
ion-segment-button {
  background: #4208ff !important;
  --background-activated: #7044ff;
  color: white !important;
  font-weight: bolder;
  --indicator-color-checked: red !important;
}
.todo-heading {
  margin-top: 3px;
  font-size: 20px;
  margin-bottom: 3px;
}
.text-strike {
  text-decoration: line-through;
}
.todays-task {
  width: 100%;
}
.no-padding {
  padding: 0px;
}






































.gery-bg {
  --background: rgb(233, 233, 233);
}

.add-notes-header {
  background: #7044ff;
  color: white;
  padding-top: 7px;
  padding-bottom: 10px;
}
.note-icon {
  font-size: 25px;
  padding-left: 10px;
  padding-right: 10px;
}
*:focus {
  outline: none;
}
.note-heading {
  font-size: 30px;
  color: #7044ff;
}
.sub-heading {
  font-size: 17px;
}
.note-input {
  width: 100% !important;
  border: none;
  min-height: 35px;
}
.input-icon {
  font-size: 30px;
  color: #7044ff;
  padding: 10px;
}
.light-bg {
  --background: white;
}

ion-card {
  --background: white !important;
  border-radius: 0px !important;
}
ion-card-subtitle {
  font-size: 20px;
  color: black;
}
.bottom-border {
  border-bottom: 1px solid rgb(225, 225, 225);
  margin-bottom: 10px;
}
ion-card-content {
  font-size: 18px !important;
}
.date {
  color: rgb(55, 173, 252);
}

.login-page {
  --background: #7044ff;
}
.logo-block {
  margin-top: 20%;
}
.logo {
  margin-left: 15px;
}
.title {
  color: white;
  font-size: 25px;
}
.sub-title {
  font-size: 22px;
  color: white;
}
.social-logo {
  width: 32px;
  margin-right: 20px;
}
